#Page1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(188deg, #0c0e64 28vh, #f3001a 28.3vh, black);
  min-height: 100vh;
}
.date-input-container {
  text-align: center;
  background: #00bffb;
  padding-top: 50px;
  width: 40vw;
}
@media screen and (max-width: 800px) {
  .date-input-container {
    width: 80vw;
  }
}
.date-input-container p {
  margin-bottom: 10px;
}
#DateInput {
  background: transparent;
  border-bottom: 1px solid black;
  width: 50%;
  text-align: center;
  color: #3d6573;
  font-weight: 900;
}
.user-info-message-container {
  background: linear-gradient(170deg, #00bffb 109px, transparent 110px);
  width: 100%;
  height: 115px;
  position: relative;
}
.user-info-message-container > div {
  background: #141414;
  color: #d9d9d9;
  position: absolute;
  text-align: center;
  height: 90px;
  width: 50%;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 800px) {
  .user-info-message-container > div {
    width: 80%;
  }
  .user-info-message-container > div > li {
    width: 80%;
    margin: auto;
  }
}
.user-info-message-container > div h2 {
  font-size: 1rem;
  font-weight: 500;
}
.user-info-message-container > div p {
  font-weight: 900;
}
ul {
  width: 40vw;
}
ul > li {
  width: 50%;
  margin: auto;
}
@media screen and (max-width: 800px) {
  ul {
    width: 80vw;
  }
  ul > li {
    width: 80%;
  }
}
nav {
  position: fixed;
  top: calc(5px * 2);
  right: calc(5px * 2);
  cursor: pointer;
  color: #d9d9d9;
}
