nav {
  position: fixed;
  top: calc(5px * 2);
  right: calc(5px * 2);
  cursor: pointer;
}
nav a {
  color: black;
}
td,
th {
  border: 1px solid #bebebe;
  padding: 10px;
}
td {
  text-align: center;
}
tr:nth-child(even) {
  background-color: #eee;
}
th[scope="col"] {
  background-color: #696969;
  color: #fff;
}
th[scope="row"] {
  background-color: #d7d9f2;
}
table {
  border-collapse: collapse;
  border: 2px solid #c8c8c8;
  letter-spacing: 1px;
  font-family: sans-serif;
  font-size: 0.8rem;
  width: 100%;
}
