

























































































































@import "~srcAlias/less/config.less";

#Page1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(188deg, @blue 28vh, @red 28.3vh, black);
  min-height: 100vh;
}

.date-input-container {
  text-align: center;
  background: @lightBlue;
  padding-top: 50px;
  width: @mainContentWidthMedium;
  @media screen and (max-width: @bpSm) {
    width: @mainContentWidthLarge;
  }
  p {
    margin-bottom: 10px;
  }
}

#DateInput {
  background: transparent;
  border-bottom: 1px solid black;
  width: 50%;
  text-align: center;
  color: @flatBlue;
  font-weight: 900;
}

.user-info-message-container {
  background: linear-gradient(170deg, @lightBlue 109px, transparent 110px);
  width: 100%;
  height: 115px;
  position: relative;
  & > div {
    background: @black;
    color: @lightTxt;
    position: absolute;
    text-align: center;
    height: 90px;
    width: 50%;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: @bpSm) {
      width: 80%;
      & > li {
        width: 80%;
        margin: auto;
      }
    }
    h2 {
      font-size: 1rem;
      font-weight: 500;
    }
    p {
      font-weight: 900;
    }
  }
}

ul {
  width: @mainContentWidthMedium;
  & > li {
    width: 50%;
    margin: auto;
  }
  @media screen and (max-width: @bpSm) {
    width: @mainContentWidthLarge;
    & > li {
      width: 80%;
    }
  }
}

nav {
  position: fixed;
  top: @spacing2;
  right: @spacing2;
  cursor: pointer;
  color: @lightTxt;
}
